import React, { ReactElement, FC } from 'react'; // importing FunctionComponent

interface IPageProps {
    title: string
}

export const PageComponent: FC<IPageProps> = ({ title, children }) => {

    return(
        <div className="w-full flex-initial overflow-hidden font-mono flex flex-col">
            <header className="flex-initial h-3.5 w-full flex flex-row justify-start text-l py-5 px-5">
                <span>{title}</span>
            </header>
            <article className="flex-initial w-full text-xs overflow-y-scroll flex flex-col  ">
                {children}
            </article>
        </div>
    )
}